<template>
  <div id="app">
    <div class="header">
      <ul class="header-button-left">
        <li v-if="step != 0" v-on:click="cancel">Cancel</li>
      </ul>

      <ul class="header-button-right">
        <!--<li v-if="step == 1" v-on:click="next">Next</li>-->
        <li v-if="step == 1" @click="step = 2">Next</li>
        <li v-if="step == 2" v-on:click="publish">발행</li>
      </ul>

      <img src="./assets/logo.png" class="logo" />
    </div>

    <Body v-bind:Datas="Datas" v-bind:step="step" v-bind:uploadImgUrl="uploadImgUrl" v-on:submitContents="contentsText = $contentsText"/>

    <div class="footer">
      <ul class="footer-button-plus">
        <input type="file" id="file" class="inputfile" v-on:change="uploadImage" />
        <label for="file" class="input-plus">+</label>
      </ul>
    </div>
  </div>
</template>

<script>
import Body from "./components/Body.vue";
import Datas from "./assets/postData";

export default {
  name: "App",
  data() {
    return {
      Datas: Datas,
      step: 0,
      uploadImgUrl : null,
      contentsText : '',
    };
  },
  components: {
    Body: Body
    //DataOrg : [...Data],
  },
  methods: {
    cancel() {
      this.step = 0;
    },
    next() {
      this.step = 2;
    },
    uploadImage(e) {
      let file = e.target.files;
      let reader = new FileReader();
      reader.readAsDataURL(file[0]);
      reader.onload = e => {
        if(e.loaded < 2096000) {
          this.uploadImgUrl = e.target.result;
        this.step = 1;
        }else{
          alert("이미지 사이즈가 2mb를 초과했습니다.");
          return false;
        }
      };
    },
    publish(){
      /*
        1. 메인화면
        2. 새포스트 데이터 생성
        3. 데이터 맨앞에 넣기
       */
      this.step = 0;
      var temp = {
        name: "작성자",
        userImage: this.uploadImgUrl,
        postImage: this.uploadImgUrl,
        likes: 0,
        date: 'June 20',
        liked: false,
        caption: this.contentsText,
        filter: "perpetua"
      }
      this.Datas.unshift(temp);
    }
  }
};
</script>

